<template lang="html">
  <div class="dialogs">
    <div class="dialog" v-bind:class="{ 'dialog-active': show }">
      <div class="dialog-content">
        <header class="dialog-header">
          <h1 class="dialog-title">{{ title }}</h1>
        </header>
        <div class="dialog-body">
          <div v-for="field in fields" class="form-group">
            <label>{{ field.name }}</label>
            <select v-if="field.dataSource" v-model="item[field.name]" :disabled="mode === 2 && field.isKey">
              <option v-for="opt in field.dataSource" :value="opt">{{ opt }}</option>
            </select>
            <input v-else type="text" v-model="item[field.name]" :disabled="mode === 2 && field.isKey">
          </div>
        </div>
        <footer class="dialog-footer">
          <div class="form-group">
            <label></label>
            <button class="btn-save" v-on:click="save">Save</button>
            <button class="btn-close" v-on:click="close">Close</button>
          </div>
        </footer>
      </div>
    </div>
    <div class="dialog-overlay"></div>
  </div>
</template>

<script>
export default {
  data () {
    return {
    };
  },
  /*
  * mode = 1是新增数据模式，mode = 2是修改数据模式
  * title表示对话框的标题内容
  * fields表示对话框要显示的数据字段数组
  * item是由simple-dialog传下来，用于绑定表单字段的
  * show是表示模态框是否显示
  */
  props: ['mode', 'title', 'fields', 'item', 'show'],
  methods: {
    close () {
      this.$emit('close', false);
    },
    save () {
      if (this.mode === 1) {
        // 使用$dispatch调用simple-grid的create-item事件
        // this.$dispatch('');
        this.$emit('create', this.item);
      } else if (this.mode === 2) {
        // 使用$dispatch调用simple-grid的update-item事件
        this.$emit('update', this.item);
      }
    }
  }
};
</script>

<style lang="css" scoped>
  .dialog {
  	width: 480px;
  	position: fixed;
  	left: 50%;
  	top: 2em;
  	transform: translateX(-50%);
  	z-index: 2000;
  	visibility: hidden;
  	backface-visibility: hidden;
  	perspective: 1300px;
  }

  .dialog-active {
  	visibility: visible;
  }

  .dialog-active .dialog-content {
  	opacity: 1;
  	transform: rotateY(0);
  }

  .dialog-active ~ .dialog-overlay {
  	opacity: 1;
  	visibility: visible;
  }

  .dialog-content {
  	border-radius: 3px;
  	background: #fff;
  	overflow: hidden;
  	box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  	transition: .5s ease-in-out;
  	opacity: 0;
  	transform-style: preserve-3d;
  	transform: rotateY(-70deg);
  }

  .dialog-header {
  	background: #0090d3;
  	color: #fff;
  }

  .dialog-title {
  	margin: 0;
  	font-size: 2em;
  	text-align: center;
  	font-weight: 200;
  	line-height: 2em;
  }

  .dialog-body {
  	padding: 2em;
  }

  .dialog-footer {
  	margin: 0 2em;
  	padding: 1em 0;
  	border-top: 1px solid rgba(0, 0, 0, 0.1);
  }

  .dialog-overlay {
  	content: "";
  	position: fixed;
  	visibility: hidden;
  	top: 0;
  	left: 0;
  	right: 0;
  	bottom: 0;
  	z-index: 1000;
  	opacity: 0;
  	background: rgba(0, 0, 0, 0.5);
  	transition: all .6s;
  }
  input {
    outline: none
  }

  input[type=text] {
    padding: 3px 6px;
    font-size: 1.2rem;
    border: 1px solid #ccc;
  }

  input[type=text]:focus {
    border-color: #0090d3;
    transition: .3s ease-in;
  }
  .form-group {
  	margin: 10px;
  }
  .form-group > label {
  	display: inline-block;
  	padding-right: 1rem;
  	width: 5rem;
  	text-align: right;
  }

  .form-group > input,
  .form-group > select {
  	display: inline-block;
  	height: 1.8rem;
  	line-height: 1.8rem;
  }
  button {
  	display: inline-block;
  	box-sizing: border-box;
  	padding: 10px 30px;
  	background: #0090d3;
  	color: #fff;
  	border: 1px solid #0090d3;
  	border-radius: 3px;
  	outline: 0;

  	transition: .4s ease-out;
  }

  button:hover,
  button:focus {
  	opacity: 0.8;
  	cursor: pointer;
  	transition: .15s ease-in;
  }
  .btn-danger{
  	padding: 5px 15px;
  	border: 1px solid salmon;
  	background: salmon;
  }

  .btn-save{
  	border: 1px solid #0090d3;
  	background: #0090d3;
  }

  .btn-close{
  	border: 1px solid #ccc;
  	background: #ccc;
  }
</style>



