<template lang="html">
  <div class="">
    <h3>-----------------class-----------------</h3>
    <p>点击li</p>
    <ul>
      <li v-for='(item,index) in message' @click='item.isred = !item.isred' :class='{ red : item.isred }'>{{item.name}}</li>
    </ul>
    <br>

    <p>当我们点击要根据需求改变更多的样式，更多的class</p>
    <div v-bind:class="classObject">v-bind:class="classObject"此方法更好</div>
    <div class="static" v-bind:class="{ active: isActive, 'text-danger': hasError }">v-bind:class="{ active: isActive, 'text-danger': hasError }"</div>
    <br>

    <p>全选按钮computed方法</p>
    <ul>
      <li v-for='(item,index) in message' @click='item.isred = !item.isred' :class='{ red : item.isred }'>{{item.name}}</li>
    </ul>
    <button :class='allchoose'>全选时变成黄色</button>
    <br>

    <p>数组写法</p>
    <ul>
      <li v-for='(item,index) in message' @click='item.isred = !item.isred' :class="['red',sizeClass]">{{item.name}}</li>
    </ul>
    <br>

    <p>三元表达式</p>
    <p>若isred为true则为红色，否则绿色</p>
    <ul>
      <li v-for='(item,index) in message' @click='item.isred = !item.isred' :class="[item.isred == true?'red':'green']">{{item.name}}</li>
    </ul>
    <br>

    <p>变量与字符串拼接</p>
    <div v-for="item in workTag" class="workTag" :class="workTagType + item.type">{{item.name}}</div>
    <br>

    <p>class切换导航active</p>
    <ul>
      <li
        v-for="(item, index) in navList"
        :class="{navActive:changeIndex === index}"
        @click="changeActive(index)"
        >
        {{item.name}}
      </li>
    </ul>
    <h3>-----------------style-----------------</h3>
    <div :style="{ fontSize: size + 'px' }">font-size:30px</div>
    <div :style="[styleObjectA, styleObjectB]">color:green</div>
  </div>
</template>

<script>
export default {
  created () {
    let data = [
      {name: 'shay帅不？'},
      {name: 'shay是老司机不？'},
      {name: 'shay分享的好不好？'}
    ];
    for (let i of data) {
      this.$set(i, 'isred', false);
    };
    this.message = data;
  },
  data () {
    return {
      message: [],
      classObject: {
        active: false,
        'text-danger': true
      },
      isActive: true,
      hasError: false,
      sizeClass: 'size',
      workTagType: 'workTag--',
      workTag: [
        {
          name: '红色',
          type: 'red'
        },
        {
          name: '蓝色',
          type: 'blue'
        }
      ],
      navList: [
        {
          name: '新闻',
          value: '1'
        },
        {
          name: '娱乐',
          value: '2'
        },
        {
          name: '体育',
          value: '3'
        },
        {
          name: '数码',
          value: '4'
        }
      ],
      changeIndex: '',
      size: 30,
      styleObjectA: {
        'color': 'green',
        'text-decoration': 'underline'
      },
      styleObjectB: {
        'background': 'red'
      }
    };
  },
  methods: {
    changeActive (n) {
      this.changeIndex = n;
    }
  },
  computed: {
    allchoose () {
      for (let i of this.message) {
        if (i.isred === false) {
          return {yellow: false};
        };
      };
      return {yellow: true};
    }
  }
};
</script>

<style lang="css" scoped>
  p{
      color: #2196f3;
  }
  .red{
      color:red;
  }
  .text-danger {
    text-decoration: underline;
  }
  .active{
    font-weight: bold;
  }
  .yellow {
    background:yellow
  }
  .green{
    color: green;
  }
  .navActive{
    font-size:20px;
    color: red;
  }
  .size {
    font-size:30px;
  }
  .workTag{
    font-style: italic;
  }
  .workTag--red{
    color: red;
  }
  .workTag--blue {
    color: blue;
  }
</style>



