<template lang="html">
  <div>
    <div v-for='(item,index) in insurance'>
      <template v-if='nowTime < item.endtime'>
        <p>code:{{item.code}}</p>
        <p>name:{{item.name}}</p>
        <p>tel:{{item.tel}}</p>
        <hr></hr>
      </template>
      <template v-else-if='nowTime > item.endtime'>
        <button @click='item.arrow = !item.arrow'>查看过保订单</button>
        <p>{{item.code}}</p>
        <p v-show='item.arrow == true'>{{item.name}}</p>
        <p v-show='item.arrow == true'>{{item.tel}}<p>
        <hr></hr>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      nowTime: '',
      insurance: []
    };
  },
  created () {
    let myDate = new Date();
    let year = myDate.getFullYear(); // 获取完整的年份(4位,1970-????)
    let month = myDate.getMonth() + 1; // 获取当前月份(0-11,0代表1月)
    let day = myDate.getDate(); // 获取当前日(1-31)
    if (day < 10) {
      day = '0' + day;
    };
    if (month < 10) {
      month = '0' + month;
    };
    this.nowTime = `${year}-${month}-${day}`;
    var insurance = [
      {endtime: '2018-02-01', code: '111111111', name: 'shay', tel: '18211103333', arrow: false},
      {endtime: '2018-02-03', code: '222222222', name: 'shay2', tel: '18211103333', arrow: false},
      {endtime: '2018-09-10', code: '333333333', name: 'shay3', tel: '18211103333', arrow: false},
      {endtime: '2017-02-09', code: '444444444', name: 'shay4', tel: '18211103333', arrow: false}
    ];
    this.insurance = insurance;
  }
};
</script>

<style lang="css">
</style>



