<template lang="html">
  <div class="">
    <myDemo :time='time' @a='clickBtn'></myDemo>
    <p>{{time}}</p>
  </div>
</template>

<script>
import myDemo from '@/components/childDemo/v-on.vue';
export default {
  data () {
    return {
      time: 0
    };
  },
  methods: {
    clickBtn (value) {
      this.time = value;
    }
  },
  components: {
    myDemo
  }
};
</script>

<style lang="css">
</style>



