import Vue from 'vue';
// 引入实例
import toast from './toast.vue';

// 用vue.extend()创建toast子实列
const ToastConstructor = Vue.extend(toast);
let removeDom = event => {
  event.target.parentNode.removeChild(event.target);
};
// 我们还是在子实列的原型上写了一个close方法，可以拿到实列上的属性，我们对$el进行transitionend动画结束监听，当动画结束时的，我们才进行删除节点，调用的是removeDom方法
ToastConstructor.prototype.close = function () {
  this.visible = false;
  this.$el.addEventListener('transitionend', removeDom);
};
const Toast = (options = {}) => {
  // 用$mount方法挂到我们自己创建的dom节点上
  // 此时的instance就是toast组件的实列了
  var instance = new ToastConstructor().$mount(document.createElement('div'));
  // 修改实例中data中的参数
  // 我们传入数据有两种形式，第一种是字符串 Toast('aaaa') 此时直接使用 options
  // 另一种是传入对象 Toast({message:'aaaa'})，此时需要用 options.message
  instance.message = typeof options === 'string' ? options : options.message;
  instance.position = options.position || 'middle';
  instance.visible = true;
  // 把创建的dom元素插入到body中
  document.body.appendChild(instance.$el);
  // toast的dom结构
  console.log(instance.$el);
  // 当我们如果没有设置显示时长，那我们则使用默认2500
  let duration = options.duration || 2500;
  // 在下次 DOM 更新循环结束之后执行延迟回调。在修改数据之后立即使用这个方法，获取更新后的 DOM
  Vue.nextTick(() => {
    instance.timer = setTimeout(function () {
      instance.close();
    }, duration);
  });
  console.log(instance);
  return instance;
};
export default Toast;



