<script>
  const serverSrc = 'www.baidu.com';
  const token = '12345678';
  const id = 1;
  const hasEnter = false;
  const userSite = '中国钓鱼岛';
  function globalAlert () {
    alert('全局函数');
  };
  export default {
    userSite, // 用户地址
    token, // 用户token身份
    id, // 用户id
    serverSrc, // 服务器地址
    hasEnter, // 用户登录状态
    globalAlert // 全局alert函数
  };
</script>



