<template lang="html">
  <div>
    <h1>比赛时间{{time}}s</h1>
    <h2>直播播报{{result}}</h2>
    <div>
      <p>中国队进球数：{{team.china}}</p>
      <button @click="team.china++">点击中国队进一球</button>
      <p>韩国队进球数：{{team.korea}}</p>
      <button @click="team.korea++">点击韩国队进一球</button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      time: 0,
      team: {
        china: 0,
        korea: 0
      }
    };
  },
  created () {
    let time = setInterval(() => {
      this.time++;
      if (this.time === 10) {
        clearInterval(time);
      }
    }, 1000);
  },
  computed: {
    result () {
      if (this.time < 10) {
        if (this.team.china > this.team.korea) {
          return '中国队领先';
        } else if (this.team.china < this.team.korea) {
          return '韩国队领先';
        } else {
          return '双方僵持';
        }
      } else {
        if (this.team.china > this.team.korea) {
          return '中国队赢';
        } else if (this.team.china < this.team.korea) {
          return '韩国队赢';
        } else {
          return '平局';
        }
      }
    }
  }
};
</script>

<style lang="css">
</style>



