<template lang="html">
  <div class="curd1">
    <input type="text" placeholder="search" @input="search" list="cars" class="search">
    <datalist id="cars">
      <option v-for="item in searchlist" :value="item"></option>
    </datalist>
    <button type="button" name="button" @click="addList">新增</button>
    <table>
      <thead>
        <tr>
          <th>id</th>
          <th>用户名</th>
          <th>邮箱</th>
          <th>性别</th>
          <th>省份</th>
          <th>爱好</th>
          <th>操作</th>
        </tr>
      </thead>
      <tbody>
        <tr v-cloak v-for="(item, index) of slist">
            <td>{{index + 1}}</td>
            <td>{{item.username}}</td>
            <td>{{item.email}}</td>
            <td>{{item.sex}}</td>
            <td>{{item.province}}</td>
            <td><span v-for="(hobby, index) of item.hobby">{{hobby.name}}|</span></td>
            <td><a href="javascript:;" @click="editList(index)">修改</a> | <a href="javascript:;" @click="delectList(item.username)">删除</a></td>
        </tr>
      </tbody>
    </table>
    <div class="overlay" v-show="showModal"></div>
    <div class="test-modal" v-show="showModal">
      <h2>{{modalTitle}}</h2>
      <table>
        <tbody>
          <tr>
            <td>用户名</td>
            <td><input type="text" v-model="newList.username"></td>
          </tr>
          <tr>
            <td>邮箱</td>
            <td><input type="text" v-model="newList.email"></td>
          </tr>
          <tr>
            <td>性别</td>
            <td>
              <input type="radio" id="male" value="male" v-model="newList.sex">
              <label for="male">男</label>
              <input type="radio" id="female" value="female" v-model="newList.sex">
              <label for="female">女</label>
              <input type="radio" id="secrecy" value="secrecy" v-model="newList.sex">
              <label for="secrecy">保密</label>
            </td>
          </tr>
          <tr>
            <td>省份</td>
            <td>
              <select v-model="newList.province">
                <option disabled value="">请选择</option>
                <option>北京</option>
                <option>上海</option>
                <option>广州</option>
                <option>河北</option>
              </select>
            </td>
          </tr>
          <tr>
            <td>爱好</td>
            <td>
              <template v-for="hobby in listHobby">
                <input type="checkbox" :id="hobby.value" :value="hobby.value" v-model="newList.hobby">
                <label :for="hobby.value">{{hobby.name}}</label>
              </template>
            </td>
          </tr>
        </tbody>
      </table>
      <button type="button" name="button" @click="closeModal">取消</button>
      <button type="button" name="button" @click="saveList">保存</button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      list: [
        {
          username: 'aaaaa',
          email: '123@qq.com',
          sex: 'male',
          province: '北京',
          hobby: [
            {
              value: 'basketball',
              name: '篮球'
            },
            {
              value: 'read',
              name: '读书'
            },
            {
              value: 'IDE',
              name: '编程'
            }
          ]
        },
        {
          username: 'bbbbb',
          email: 'bbbbbbb@163.com',
          sex: 'female',
          province: '河北',
          hobby: [{
            value: 'basketball',
            name: '篮球'
          },
          {
            value: 'read',
            name: '读书'
          }]
        }
      ],
      listHobby: [
        {
          value: 'basketball',
          name: '篮球'
        },
        {
          value: 'read',
          name: '读书'
        },
        {
          value: 'IDE',
          name: '编程'
        },
        {
          value: 'draw',
          name: '插画'
        },
        {
          value: 'play',
          name: '弹琴'
        }
      ],
      showModal: false,
      modalTitle: '新增',
      editIndex: '',
      newList: {
        username: '',
        email: '',
        sex: '',
        province: '',
        hobby: []
      },
      slist: [],
      searchlist: []
    };
  },
  mounted () {
    this.setSlist(this.list);
  },
  watch: {
    list (val, oldval) {
      this.setSlist(val);
    }
  },
  methods: {
    setSlist (arr) {
      this.slist = JSON.parse(JSON.stringify(arr));
    },
    // 搜索
    search (e) {
      var v = e.target.value; // input输入的值
      var self = this;
      self.searchlist = []; // 用户可能搜索的词语
      if (v) {
        var ss = []; // 保存过滤后的数据
        // 过滤需要的数据
        this.list.forEach(function (item) {
          // 检测用户名
          if (item.username.indexOf(v) > -1) { // 过滤出用户名
            if (self.searchlist.indexOf(item.username) === -1) { // 如果在搜索词语数组中没有此用户名
              self.searchlist.push(item.username); // 把用户名push进可能要搜索的词语数组中
            }
            ss.push(item);
          } else if (item.email.indexOf(v) > -1) {
            // 检测邮箱
            if (self.searchlist.indexOf(item.email) === -1) {
              self.searchlist.push(item.email);
            }
            ss.push(item);
          }
        });
        this.setSlist(ss); // 将过滤后的数据给了slist
      } else {
        // 没有搜索内容，则展示全部数据
        this.setSlist(this.list);
      }
    },
    // 点击新增弹框
    addList () {
      this.editIndex = ''; // 下标为空时则为新增
      this.modalTitle = '新增'; // 模态框标题
      this.showModal = true; // 是否显示模态框 true时显示
      this.newList = { // 清空模态框中input数据
        username: '',
        email: '',
        sex: 'male',
        province: '',
        hobby: []
      };
    },
    // 点击修改弹框
    editList (n) {
      this.modalTitle = '修改'; // 模态框标题
      this.editIndex = n; // 下标为需要修改的数据下标
      console.log(this.editIndex);
      // 需要深度拷贝，之前的写法：this.newList = this.list[this.editIndex];
      this.newList = JSON.parse(JSON.stringify(this.list[this.editIndex])); // 先转换为字符串，然后再转换
      let checked = [];
      this.list[this.editIndex].hobby.forEach(function (item) {
        checked.push(item.value); // 获得喜好
      });
      this.newList.hobby = checked;
      this.showModal = true;
    },
    // 保存
    saveList () {
      let obj = JSON.parse(JSON.stringify(this.newList)); // 接收要增加或修改的数据
      // obj中的hobby数据只有复选框的value，并没有name
      // 需要在listHobby中来找到hobby的name并组成对象
      let checkValue = obj.hobby;
      let checked = [];
      for (var i = 0; i < checkValue.length; i++) {
        let name;
        for (var j = 0; j < this.listHobby.length; j++) {
          let value = this.listHobby[j].value;
          if (value.indexOf(checkValue[i]) > -1) {
            name = this.listHobby[j].name; // 找到hobby的name
            break;
          };
        };
        let a = { // 创建对象
          value: checkValue[i],
          name: name
        };
        checked.push(a);
      }
      obj.hobby = checked;
      if (this.editIndex !== '') {
        // 修改list后出发watch
        this.$set(this.list, this.editIndex, obj);
        // this.list[this.editIndex] = obj;
        // this.setSlist(this.list);
      } else {
        this.list.push(obj);
      }
      this.showModal = false;
    },
    // 删除
    delectList (n) {
      let index;
      for (var i = 0; i < this.list.length; i++) {
        if (n.indexOf(this.list[i].username) > -1) {
          console.log(i);
          index = i;
        }
      }
      this.list.splice(index, 1);
    },
    // 取消
    closeModal () {
      this.showModal = false;
    }
  }
};
</script>

<style lang="css" scoped>
  table{
    border-collapse: collapse;
    margin-bottom: 20px;
  }
  th,td{
    padding: 5px;
    border: 1px solid #ccc;
  }
  .overlay{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1500;
    width: 100%;
    height: 100%;
    opacity: 0.5;
    background: #000;
  }
  .test-modal{
    position: fixed;
    left: 50%;
    top: 50%;
    margin-top: -170px;
    margin-left: -210px;
    z-index: 1800;
    width: 420px;
    min-height: 340px;
    background: #fff;
    overflow: hidden;
    padding: 20px;
    box-sizing: border-box;
  }
</style>



