<template lang="html">
  <div id="login" ref="userInfo">
    <a class="login" @click="show = !show">点击菜单</a>
    <transition name="info">
      <div v-if="show" class="user-info">
        <div class="user-head"></div>
        <div class="user-name">astonmartin</div>
        <div class="my-channel">我的频道</div>
        <div class="logout">退出</div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  data () {
    return {
      show: false
    };
  },
  mounted () {
    let _this = this;
    document.addEventListener('click', function (e) {
      // console.log(e.target);
      // console.log(!_this.$refs.userInfo.contains(e.target));
      // .contains 包含
      if (!_this.$refs.userInfo.contains(e.target)) {
        _this.show = false;
      } else {
        _this.show = true;
      };
    });
  }
};
</script>

<style lang="css">
/*
 *v-enter: 定义进入过渡的开始状态。在元素被插入时生效，在下一个帧移除。
 *v-enter-active: 定义进入过渡的结束状态。在元素被插入时生效，在 transition/animation 完成之后移除。
 *v-leave: 定义离开过渡的开始状态。在离开过渡被触发时生效，在下一个帧移除。
 *v-leave-active: 定义离开过渡的结束状态。在离开过渡被触发时生效，在 transition/animation 完成之后移除。
 */
a{
  cursor: pointer;
}
.info-enter,.info-leave-active { /*开始前，和结束时*/
  opacity: 0;
  transform: translate3d(0,20%,0);
}
.info-enter-active,.info-leave-active {
  transition: all .5s cubic-bezier(0.4, 0, 0, 1.5);
}

</style>



