<template lang="html">
  <div>
    <count-down :start='start' @countDown='start=false' @click.native='sendCode'></count-down>
  </div>
</template>

<script>
import countDown from '@/components/childDemo/childCountDown.vue';
export default {
  data () {
    return {
      start: false
    };
  },
  methods: {
    sendCode (value) {
      // 前面发送ajax请求成功之后调用this.start = true开始短信倒计时
      this.start = true;
    }
  },
  components: {
    countDown
  }
};
</script>

<style lang="css">
</style>



