<template lang="html">
  <label>
    <input type="radio" :checked="shouldBeChecked" :name="name" :value="value" @change="updateInput">
    {{ label }}
  </label>
</template>

<script>
export default {
  model: {
    prop: 'modelValue',
    event: 'change'
  },
  props: {
    value: {
      type: String
    },
    name: {
      type: String
    },
    modelValue: {
      default: ''
    },
    label: {
      type: String,
      required: true
    }
  },
  computed: {
    shouldBeChecked () {
      return this.modelValue === this.value;
    }
  },
  methods: {
    updateInput () {
      this.$emit('change', this.value);
    }
  }
};
</script>

<style lang="css">
</style>



