<template lang="html">
  <li>
    <slot></slot>
  </li>
</template>

<script>
export default {
  mounted () {
    this.$nextTick(() => {
      this.$parent.destroy();
      this.$parent.init();
      this.$parent.start();
    });
  }
};
</script>

