<template lang="html">
  <div class="spinner">
    <snake color="gray"></snake>
    <snake :color="colorType"></snake>
    <doubleBounce></doubleBounce>
  </div>
</template>

<script>
import snake from '@/components/snake/snake.vue';
import doubleBounce from '@/components/double-bounce/double-bounce.vue';

export default {
  data () {
    return {
      colorType: 'blue',
      color: 'green'
    };
  },
  components: {
    snake,
    doubleBounce
  }
};
</script>

<style lang="css">
</style>



