<template lang="html">
  <div>
    <button @click='demo'>到五的时候取消监听</button>
    <p>{{count1}}</p>
    <button @click='demo2'>只监听一次,传一个参过来</button>
    <p>{{count2}}</p>
  </div>
</template>

<script>
export default {
  data () {
    return {
      count1: 0,
      count2: 0
    };
  },
  created () {
    // 监听 类似具体的方法
    this.$on('demo', () => {
      this.count1++;
      if (this.count1 === 5) {
        // 取消监听
        this.$off('demo');
      };
    });
    this.$once('demo2', (value) => {
      this.count2 += value;
    });
  },
  methods: {
    demo () {
      // 触发demo函数 类似触发器
      this.$emit('demo');
    },
    demo2 () {
      // 传参
      this.$emit('demo2', 10);
    }
  }
};
</script>

<style lang="css">
</style>



