<template lang="html">
  <div class="">
    <table>
      <thead>
        <tr>
          <th v-for="col in columns">{{ col.name }}</th>
          <th>Delete</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(entry, index) in dataList">
          <td v-for="col in columns">
            <span v-if="col.isKey"><a href="javascript:void(0)" @click="openEditItemDialog(entry[col.name])">{{entry[col.name]}}</a></span>
            <span v-else>{{entry[col.name]}}</span>
          </td>
          <td class="text-center">
            <button class="btn-danger" @click="deleteItem(entry)">delete</button>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="container">
      <button class="btn" @click="openNewItemDialog('Create New Item')">Create</button>
    </div>
    <modal-dialog :show="showDialog" :mode="mode" :title="title" :item="item" :fields="columns" @close="closeModal" @create="createItem" @update="updateItem"></modal-dialog>
  </div>
</template>

<script>
import modalDialog from '@/components/CURD/modal-dialog';
export default {
  props: ['dataList', 'columns', 'searchKey'],
  data () {
    return {
      mode: 0,
      title: '',
      keyColumn: '',
      item: {},
      showDialog: false
    };
  },
  mounted () {
    for (var i = 0; i < this.columns.length; i++) {
      if (this.columns[i].isKey) {
        this.keyColumn = this.columns[i]['name'];
        console.log(this.keyColumn);
        break;
      };
    };
  },
  methods: {
    openNewItemDialog (title) {
      // 对话框的标题
      this.title = title;
      // mode = 1表示新建模式
      this.mode = 1;
      // 初始化this.item
      this.item = {};
      // 广播事件，showDialog是modal-dialog组件的一个方法，传入参数true表示显示对话框
      this.showDialog = true;
    },
    openEditItemDialog (key) {
      // 根据主键查找当前修改的数据
      var currentItem = this.findItemByKey(key);
      // 对话框的标题
      this.title = 'Edit Item - ' + key;
      // mode = 2表示修改模式
      this.mode = 2;
      // 将选中的数据拷贝到this.item
      this.item = this.initItemForUpdate(currentItem);
      // 广播事件，传入参数true表示显示对话框
      this.showDialog = true;
    },
    // 弹出修改数据的对话框时，使用对象的深拷贝
    initItemForUpdate (p, c) {
      c = c || {};
      for (var i in p) {
        // 属性i是否为p对象的自有属性
        if (p.hasOwnProperty(i)) {
          // 属性i是否为复杂类型
          if (typeof p[i] === 'object') {
            // 如果p[i]是数组，则创建一个新数组
            // 如果p[i]是普通对象，则创建一个新对象
            c[i] = Array.isArray(p[i]) ? [] : {};
            // 递归拷贝复杂类型的属性
            this.initItemForUpdate(p[i], c[i]);
          } else {
            // 属性是基础类型时，直接拷贝
            c[i] = p[i];
          };
        };
      };
      return c;
    },
    // 根据主键查找当前修改的数据
    findItemByKey (key) {
      var keyColumn = this.keyColumn;
      for (var i = 0; i < this.dataList.length; i++) {
        if (this.dataList[i][keyColumn] === key) {
          return this.dataList[i];
        };
      };
    },
    // 判断名字是否重复
    itemExists () {
      var keyColumn = this.keyColumn;
      for (var i = 0; i < this.dataList.length; i++) {
        console.log(this.dataList[i][keyColumn]);
        if (this.item[keyColumn] === this.dataList[i][keyColumn]) {
          return true;
        };
      };
      return false;
    },
    createItem (data) {
      var keyColumn = this.keyColumn;
      if (!this.itemExists()) {
        // 将item追加到dataList
        this.dataList.push(data);
        // 新建完数据后，重置item对象
        this.item = {};
      } else {
        alert(keyColumn + ' "' + this.item[keyColumn] + '" 重复了');
      };
      // 广播事件，传入参数false表示隐藏对话框
      this.showDialog = false;
    },
    updateItem (data) {
      // 获取主键列
      var keyColumn = this.keyColumn;
      for (var i = 0; i < this.dataList.length; i++) {
        // 根据主键查找要修改的数据，然后将this.item数据更新到this.dataList[i]
        if (this.dataList[i][keyColumn] === this.item[keyColumn]) {
          for (var j in this.item) {
            this.dataList[i][j] = this.item[j];
          };
          break;
        };
      };
      // 修改完数据后，重置item对象
      this.item = {};
      // 广播事件，传入参数false表示隐藏对话框
      this.showDialog = false;
    },
    closeModal (data) {
      this.showDialog = data;
    },
    deleteItem (entry) {
      var data = this.dataList;
      // 找到相同数据，并且删除
      data.forEach(function (item, i) {
        if (item === entry) {
          data.splice(i, 1);
          return;
        };
      });
    }
  },
  components: {
    modalDialog
  }
};
</script>

<style lang="css" scoped>
  table,
  td,
  th {
  	border-collapse: collapse;
  	border-spacing: 0
  }

  table {
  	width: 100%;
  }

  td,
  th {
  	border: 1px solid #bcbcbc;
  	padding: 5px 10px;
  }

  th {
  	padding: 10px;
  	font-weight: 400;
  	color: #fff;
  	background: #0090d3;
  	cursor: pointer;
  }

  tr:nth-of-type(odd) {
  	background: #fff
  }

  tr:nth-of-type(even) {
  	background: #eee
  }

  h1{
  	font-size: 1.5rem;
  	margin-bottom: 2rem;
  }

  input {
  	outline: none
  }

  input[type=text] {
  	padding: 3px 6px;
  	font-size: 1.2rem;
  	border: 1px solid #ccc;
  }

  input[type=text]:focus {
  	border-color: #0090d3;
  	transition: .3s ease-in;
  }

  button {
  	display: inline-block;
  	box-sizing: border-box;
  	padding: 10px 30px;
  	background: #0090d3;
  	color: #fff;
  	border: 1px solid #0090d3;
  	border-radius: 3px;
  	outline: 0;

  	transition: .4s ease-out;
  }

  button:hover,
  button:focus {
  	opacity: 0.8;
  	cursor: pointer;
  	transition: .15s ease-in;
  }
  .btn-danger{
  	padding: 5px 15px;
  	border: 1px solid salmon;
  	background: salmon;
  }

  .btn-save{
  	border: 1px solid #0090d3;
  	background: #0090d3;
  }

  .btn-close{
  	border: 1px solid #ccc;
  	background: #ccc;
  }


  .container {
  	padding-left: 15px;
  	padding-right: 15px;
  	margin: 10px;
  }

  .form-group {
  	margin: 10px;
  }
  .form-group > label {
  	display: inline-block;
  	padding-right: 1rem;
  	width: 5rem;
  	text-align: right;
  }

  .form-group > input,
  .form-group > select {
  	display: inline-block;
  	height: 1.8rem;
  	line-height: 1.8rem;
  }

  .text-center {
  	text-align: center;
  }
</style>



