<template lang="html">
  <div class="">
    <table class="table1">
        <thead>
          <tr>
            <th>#</th>
            <th>Name</th>
            <th>Age</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td><input type="checkbox" v-model="checkedAll" @change="toggleCheckAll"/>全选</td>
          </tr>
          <tr v-for="item in list">
            <td><input type="checkbox" v-model="item.checked" @change="handleCheckChange"/></td>
            <td>{{item.name}}</td>
            <td>{{item.age}}</td>
          </tr>
        </tbody>
    </table>
  </div>
</template>

<script>

export default {
  data () {
    return {
      checkedAll: false,
      list: [
        {
          name: '张三',
          age: 23,
          checked: false
        },
        {
          name: '李四',
          age: 20,
          checked: true
        },
        {
          name: '王五',
          age: 30,
          checked: true
        }
      ]
    };
  },
  methods: {
    toggleCheckAll () {
      let value = this.checkedAll;
      this.list.forEach(function (item) {
        item.checked = value;
      });
    },
    handleCheckChange () {
      var i = 0;
      this.list.forEach(function (item) {
        if (item.checked === true) i++;
      });
      console.log(i);
      if (i === this.list.length) {
        this.checkedAll = true;
      } else {
        this.checkedAll = false;
      }
      console.log(this.checkedAll);
    }
  }
};
</script>

<style lang="css">
</style>



