<template lang="html">
  <div class="vux-marquee" :style="{height: height + 'px'}">
    <ul class="vux-marquee-box" ref="box" :style="{transform: `translate3d(0,${currenTranslateY}px,0)`, transition: `transform ${noAnimate ? 0 : duration}ms`}">
      <slot></slot>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    interval: { // 每个marquee条目切换间隔时间
      type: Number,
      default: 2000
    },
    duration: { // 动画过渡时长
      type: Number,
      default: 300
    },
    direction: { // 配置上下方向
      type: String,
      default: 'up'
    }
  },
  data () {
    return {
      currenTranslateY: 0, // 改变的y轴解度存放
      height: '', // 高度存放的值存放
      length: 0, // marquee条目的总长度存放
      currentIndex: 0, // 当前每个条目的index下标值存放
      noAnimate: false // 是否是进行动画的配置值存放
    };
  },
  beforeDestroy () {
    this.destroy();
  },
  methods: {
    destroy () {
      this.timer && clearInterval(this.timer);
    },
    // 初始化配置方法
    init () {
      this.destroy();
      if (this.cloneNode) {
        this.$refs.box.removeChild(this.cloneNode);
      };
      this.cloneNode = null; // 进行存放，一旦重新渲染时可以删除标记
      let firstItem = this.$refs.box.firstElementChild; // 获取box节点下的第一个元素
      if (!firstItem) { // 如果没有li元素则退出
        return false;
      };
      this.length = this.$refs.box.children.length; // 获得ul下li节点的长度
      this.height = firstItem.offsetHeight;
      // 无缝滚动操作配置
      // 如果是 up 向上滚动，就深度复制第一条目进行最后节点插入
      // 如果是down,就对最后一个条目深度复制插入到第一个条目前
      if (this.direction === 'up') {
        this.cloneNode = firstItem.cloneNode(true);
        this.$refs.box.appendChild(this.cloneNode);
      } else {
        this.cloneNode = this.$refs.box.lastElementChild.cloneNode(true);
        this.$refs.box.insertBefore(this.cloneNode, firstItem);
      }
    },
    start () {
      if (this.direction === 'down') this.go(false); // 对初始样式方向
      this.timer = setInterval(() => {
        console.log(this.currentIndex);
        if (this.direction === 'up') {
          this.currentIndex += 1;
          this.currenTranslateY = -this.currentIndex * this.height;
        } else {
          this.currentIndex -= 1;
          this.currenTranslateY = -(this.currentIndex + 1) * this.height;
        }
        if (this.currentIndex === this.length) {
          setTimeout(() => {
            this.go(true);
          }, this.duration);
        } else if (this.currentIndex === -1) {
          setTimeout(() => {
            this.go(false);
          }, this.duration);
        } else {
          this.noAnimate = false;
        }
      }, this.interval + this.duration);
    },
    go (toFirst) {
      this.noAnimate = true;
      if (toFirst) {
        this.currentIndex = 0;
        this.currenTranslateY = 0;
      } else {
        this.currentIndex = this.length - 1; // 当我们向下marquee的时候，此时最后一个下标为总长度-1
        this.currenTranslateY = -(this.currentIndex + 1) * this.height;
        // 因为如果向下的话，我们在li的最顶部插入了最后一dom此时我们要+1
      }
    }
  }
};
</script>

<style lang="css">
.vux-marquee {
  width: 100%;
  overflow:hidden;
}
.vux-marquee-box {
  padding: 0;
  margin: 0;
  width: 100%;
  height: auto;
}
.vux-marquee-box li {
  margin: 0;
  width: 100%;
  padding:10px 0;
  box-sizing:border-box;
}
</style>



