<template lang="html">
  <div id="example">
  <div v-for="(item, index) in list">
      <input type="checkbox" v-model="item.ischecked" @change="change1(index)" /> {{ item.test }}
      <p v-for="(item2, index2) in item.children"  style="margin-left: 20px;">
        <input type="checkbox" v-model="item2.ischecked" @change="change2(item2, index)" /> {{ item2.test }}
      </p>
  </div>
</div>
</template>

<script>
export default {
  data () {
    return {
      list: [{
        ischecked: false,
        test: '1',
        children: [{
          ischecked: false,
          test: 11
        }, {
          ischecked: false,
          test: 21
        }, {
          ischecked: false,
          test: 31
        }]
      }, {
        ischecked: false,
        test: '2',
        children: [{
          ischecked: false,
          test: 12
        }, {
          ischecked: false,
          test: 22
        }, {
          ischecked: false,
          test: 32
        }]
      }, {
        ischecked: false,
        test: '3',
        children: [{
          ischecked: false,
          test: 13
        }, {
          ischecked: false,
          test: 23
        }, {
          ischecked: false,
          test: 33
        }]
      }, {
        ischecked: false,
        test: '4',
        children: [{
          ischecked: false,
          test: 14
        }, {
          ischecked: false,
          test: 24
        }, {
          ischecked: false,
          test: 34
        }]
      }]
    };
  },
  methods: {
    change1 (index) {
      this.resetall(index);
      var obj = this.list[index];
      // 使当前数组中的所有checkbox都为true
      var arr = obj.children.map(function (item) {
        item.ischecked = obj.ischecked;
        return item;
      });
      obj.children = arr;
      this.$set(this.list, index, obj);
    },
    change2 (item, pindex) {
      this.resetall(pindex);
      var obj = this.list[pindex];
      obj.ischecked = this.checkall(obj.children);
      this.$set(this.list, pindex, obj);
    },
    resetall (pindex) {
      var self = this;
      var value = self.list.map(function (item, index) {
        // 当前点击的下标与list中一个个对比，找到除当前外的数组
        if (pindex !== index) {
          item.ischecked = false;
          // 使每一个checkbox都为false
          item.children = item.children.map(function (item1, index1) {
            item1.ischecked = false;
            return item1;
          });
        }
        return item;
      });
      this.list = value;
    },
    checkall (arr) {
      var r = true;
      arr.forEach(function (item) {
        if (!item.ischecked) {
          r = false;
          return false;
        }
      });
      return r;
    }
  }
};
</script>

<style lang="css">
</style>



