<template lang="html">
  <div>
    <button @click='a'>点击1</button>
    <button @click='b'>点击2</button>
  </div>
</template>

<script>
import Toast from '@/components/toast/toast.js';
export default {
  methods: {
    a () {
      Toast('只是一个文字弹出');
    },
    b () {
      Toast({
        message: '有位置的设定和时间设定',
        position: 'top',
        duration: 1000
      });
    }
  }
};
</script>

<style lang="css">
</style>



