<template lang="html">
  <div class='snake' :style="{
    'border-top-color' : spinnerColor,
    'border-left-color' : spinnerColor,
    'border-bottom-color' : spinnerColor,
    'height' : spinnerSize,
    'width' : spinnerSize
    }">
  </div>
</template>

<script>
export default {
  name: 'snake',
  props: {
    color: String,
    size: Number
  },
  computed: {
    spinnerColor () {
      // 可以找到父组件data中color的值
      console.log(this.$parent.color);
      return this.color || this.$parent.color || 'red';
    },
    spinnerSize () {
      // props传值 || 父组件中的data || 默认size
      return (this.size || this.$parent.size || 50) + 'px';
    }
  }
};
</script>

<style lang="css">
.snake {
  animation : shay-spinner-rotate 0.8s infinite linear;
  border : 4px solid transparent;
  border-radius : 50%;
}
@keyframes  shay-spinner-rotate {
  0% {
    transform : rotate(0deg);
    transform : rotate(360deg)
  }
}
</style>



