<template lang="html">
  <div class="">
    <button @click="openTop()">top</button>
    <button @click="openCenter()">center</button>
    <button @click="openBottom()">bottom</button>
    <button @click="openLoading()">loading</button>
  </div>
</template>

<script>
export default {
  methods: {
    openTop () {
      this.$toast('上上上', 'center');
    },
    openCenter () {
      this.$toast.center('中中中');
    },
    openBottom () {
      this.$toast('下下下');  // or this.$toast.bottom('bottom');
    },
    openLoading () {
      this.$loading('loading...');
      let self = this;
      setTimeout(function () {
        self.closeLoading();
      }, 2000);
    },
    closeLoading () {
      this.$loading.close();
    }
  }
};
</script>

<style lang="css">
</style>



