<template lang="html">
  <div class="">
    <input type="file" name="" value="" @change="uppic">
  </div>
</template>

<script>
import $ from 'jquery';
export default {
  methods: {
    // 上传图片，绑定change事件
    uppic (e) {
      var file = e.target.files[0]; // 每次只允许上传一张图片，因此只取[0]
      // 判断图片格式
      if (file.type !== 'image/png') {
        alert('图片格式不正确');
        e.target.files.length = 0;
        $(e.target).val('');
        return false;
      }
      // 使用formData组装数据
      var formData = new FormData();
      formData.append('pic', $(e.target)[0].files[0]); // 文件数据
      formData.append('flag', '1'); // 其他的一些参数
      // $.ajax({ // ajax上传
      //   url: 'xxxxx.php',
      //   type: 'POST',
      //   cache: false,
      //   data: formData,
      //   processData: false,
      //   contentType: false
      //   }).done(function(result) {
      //   console.log('上传完成');
      // });
    }
  }
};
</script>

<style lang="css">
</style>



