<template lang="html">
  <div class="CURD3">
    <div class="form-group">
      <label>Search</label>
      <input type="text" class="search-input" v-model="searchQuery" />
    </div>
    <simple-grid :data-list="people" :columns="columns" :search-key="searchQuery"></simple-grid>
  </div>
</template>

<script>
import simpleGrid from '@/components/CURD/simple-grid';

export default {
  data () {
    return {
      searchQuery: '',
      columns: [{
        name: 'name',
        isKey: true
      }, {
        name: 'age'
      }, {
        name: 'sex',
        dataSource: ['Male', 'Female']
      }],
      people: [{
        name: 'Jack',
        age: 30,
        sex: 'Male'
      }, {
        name: 'Bill',
        age: 26,
        sex: 'Male'
      }, {
        name: 'Tracy',
        age: 22,
        sex: 'Female'
      }, {
        name: 'Chris',
        age: 36,
        sex: 'Male'
      }]
    };
  },
  components: {
    simpleGrid
  }
};
</script>

<style lang="css" scoped>
  * {
  	margin: 0;
  	padding: 0;
  	box-sizing: border-box;
  	font-family: Helvetica, simhei, Arial, sans-serif;
  }

  html {
  	font-size: 1rem;
  }

  body{
  	margin-top: 100px;
  }
  .CURD3{
    margin: 0 auto;
    max-width: 640px;
  }
  .form-group {
    margin: 10px;
  }
  .form-group > label {
    display: inline-block;
    padding-right: 1rem;
    width: 5rem;
    text-align: right;
  }
  .form-group > input,
  .form-group > select {
    display: inline-block;
    height: 1.8rem;
    line-height: 1.8rem;
  }
  input[type=text] {
    padding: 3px 6px;
    font-size: 1.2rem;
    border: 1px solid #ccc;
  }

  input[type=text]:focus {
    border-color: #0090d3;
    transition: .3s ease-in;
  }
  .search-input {
    width: 80%;
  }
</style>



