import Vue from 'vue';
import Router from 'vue-router';
// import HelloWorld from '@/components/HelloWorld';
import index from '@/view/index/index';
import classStyle from '@/view/class-style/class-style';
import TodoList from '@/view/TodoList/TodoList';
import selectAll1 from '@/view/selectAll/selectAll1';
import selectAll2 from '@/view/selectAll/selectAll2';
import treeCheck from '@/view/selectAll/treeCheck';
import tabMenu from '@/view/tab/tabMenu';
import tabMenu1 from '@/view/tab/tabMenu1';
import CURD1 from '@/view/CURD/CURD1';
import CURD2 from '@/view/CURD/CURD2';
import CURD3 from '@/view/CURD/CURD3';
import pagination1 from '@/view/pagination/page1';
import filtersPhone from '@/view/filters/phone';
import footerball from '@/view/computed/footerball';
import watchImg from '@/view/watch/img';
import safe from '@/view/if-for/safe';
import emitDemo from '@/view/v-on/emitDemo';
import fatherDemo from '@/view/v-on/fatherDemo';
import globalVar from '@/view/globalVar/globalVar';
import countDown from '@/view/v-on/countDown';
import spinner from '@/view/spinner/spinner';
import spinnerCompoents from '@/view/spinner/spinnerCompoents';
import toastPage from '@/view/toastPage/toastPage';
import toastJs from '@/view/toastPage/toastJs';
import timeDownPage from '@/view/timeDownPage/timeDownPage';
import marqueePage from '@/view/marqueePage/marqueePage';
import vuexDemo from '@/view/vuex/vuexDemo';
import inputModel from '@/view/input-model/input-model';
import transitionNav from '@/view/transition/nav';
import transitionChangePage from '@/view/transition/changePage';
import hookConsole from '@/view/hookFunction/hookConsole';
import clickAnimeJs from '@/view/clickAnimeJs/clickAnimeJs';
import fileup from '@/view/fileup/fileup';
import tips from '@/view/tips/tips';
import globalSass from '@/view/globalSass/globalSass';
import myRouter from '@/view/myRouter/myRouter';
import routerMessage from '@/components/myRouter/message';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '*',
      component: resolve => require(['../view/404/404.vue'], resolve)
    },
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/classStyle',
      name: 'classStyle',
      component: classStyle
    },
    {
      path: '/TodoList',
      name: 'TodoList',
      component: TodoList
    },
    {
      path: '/selectAll1',
      name: 'selectAll1',
      component: selectAll1
    },
    {
      path: '/selectAll2',
      name: 'selectAll2',
      component: selectAll2
    },
    {
      path: '/tabMenu',
      name: 'tabMenu',
      component: tabMenu
    },
    {
      path: '/tabMenu1',
      name: 'tabMenu1',
      component: tabMenu1
    },
    {
      path: '/treeCheck',
      name: 'treeCheck',
      component: treeCheck
    },
    {
      path: '/CURD1',
      name: 'CURD1',
      component: CURD1
    },
    {
      path: '/CURD2',
      name: 'CURD2',
      component: CURD2
    },
    {
      path: '/CURD3',
      name: 'CURD3',
      component: CURD3
    },
    {
      path: '/pagination1',
      name: 'pagination1',
      component: pagination1
    },
    {
      path: '/filtersPhone',
      name: 'filtersPhone',
      component: filtersPhone
    },
    {
      path: '/footerball',
      name: 'footerball',
      component: footerball
    },
    {
      path: '/watchImg',
      name: 'watchImg',
      component: watchImg
    },
    {
      path: '/safe',
      name: 'safe',
      component: safe
    },
    {
      path: '/emitDemo',
      name: 'emitDemo',
      component: emitDemo
    },
    {
      path: '/fatherDemo',
      name: 'fatherDemo',
      component: fatherDemo
    },
    {
      path: '/globalVar',
      name: 'globalVar',
      component: globalVar
    },
    {
      path: '/countDown',
      name: 'countDown',
      component: countDown
    },
    {
      path: '/spinner',
      name: 'spinner',
      component: spinner
    },
    {
      path: '/spinnerCompoents',
      name: 'spinnerCompoents',
      component: spinnerCompoents
    },
    {
      path: '/toastPage',
      name: 'toastPage',
      component: toastPage
    },
    {
      path: '/toastJs',
      name: 'toastJs',
      component: toastJs
    },
    {
      path: '/timeDownPage',
      name: 'timeDownPage',
      component: timeDownPage
    },
    {
      path: '/marqueePage',
      name: 'marqueePage',
      component: marqueePage
    },
    {
      path: '/vuexDemo',
      name: 'vuexDemo',
      component: vuexDemo
    },
    {
      path: '/inputModel',
      name: 'inputModel',
      component: inputModel
    },
    {
      path: '/transitionNav',
      name: 'transitionNav',
      component: transitionNav
    },
    {
      path: '/transitionChangePage',
      name: 'transitionChangePage',
      component: transitionChangePage
    },
    {
      path: '/hookConsole',
      name: 'hookConsole',
      component: hookConsole
    },
    {
      path: '/clickAnimeJs',
      name: 'clickAnimeJs',
      component: clickAnimeJs
    },
    {
      path: '/fileup',
      name: 'fileup',
      component: fileup
    },
    {
      path: '/tips',
      name: 'tips',
      component: tips
    },
    {
      path: '/globalSass',
      name: 'globalSass',
      component: globalSass
    },
    {
      path: '/myRouter',
      name: 'myRouter',
      component: myRouter,
      children: [
        {path: '', component: resolve => require(['../components/myRouter/default.vue'], resolve), name: 'default'},
        {path: '/home', component: resolve => require(['../components/myRouter/home.vue'], resolve), name: '主页'},
        {path: 'user', component: resolve => require(['../components/myRouter/user.vue'], resolve), name: '用户'},
        {path: 'routerMessage/:title', component: routerMessage, name: 'message'}
      ]
    }
  ]
});



