<template lang="html">
  <div class="">
    <p>{{time}}</p>
  </div>
</template>

<script>
export default{
  data () {
    return {
      time: '',
      flag: false
    };
  },
  mounted () { // 500毫秒后启动定时器
    let time = setInterval(() => {
      if (this.flag === true) {
        clearInterval(time);
      };
      this.timeDown();
    }, 500);
  },
  props: {
    endTime: {
      type: String
    }
  },
  methods: {
    timeDown () {
      const endTime = new Date(this.endTime);
      const nowTime = new Date();
      let leftTime = parseInt((endTime.getTime() - nowTime.getTime()) / 1000);
      let d = parseInt(leftTime / (24 * 60 * 60));
      let h = this.format(parseInt(leftTime / (60 * 60) % 24));
      let m = this.format(parseInt(leftTime / 60 % 60));
      let s = this.format(parseInt(leftTime % 60));
      if (leftTime <= 0) {
        this.flag = true;
        // 触发time-end message = '倒计时结束'
        this.$emit('time-end');
      };
      this.time = `${d}天${h}小时${m}分${s}秒`;
    },
    format (time) { // 设置时间格式
      if (time >= 10) {
        return time;
      } else {
        return `0${time}`;
      }
    }
  }
};
</script>

<style lang="css">
</style>



