<template lang="html">
  <div>
    <marquee direction='down'> <!-- 如果不写 direction='down' 则默认为 up -->
      <marquee-item class='bb' v-for="i in number" :key = "i">shay{{i}}</marquee-item>
    </marquee>
  </div>
</template>

<script>
import Marquee from '@/components/marquee/marquee.vue';
import MarqueeItem from '@/components/marquee/marquee-item.vue';
export default {
  data () {
    return {
      number: 10
    };
  },
  components: {
    Marquee,
    MarqueeItem
  }
};
</script>

<style lang="css">
.bb{
    font-size:20px;
}
</style>



