<template lang="html">
  <div class="">
    <div class="hd">
      <ul class="clearfix">
        <li v-for="(item, index) of list" :class="{active:selected==index}" @mouseenter="change(index)">{{item.title}}</li>
      </ul>
    </div>
    <component :is="currentView"></component>
  </div>
</template>

<script>
import Vue from 'vue';
// tab0
Vue.component('item0', {
  template: '<div>1111111content</div>'
});
// tab1
Vue.component('item1', {
  template: '<div>222222content</div>'
});
// tab2
Vue.component('item2', {
  data () {
    return {
      message: ''
    };
  },
  template: `<div>
  <span style="color:#f00">hello world</span>
  <p><input type="text" v-model="message"></p>
  <p>{{message}}</p>
  </div>`
});
export default {
  data () {
    return {
      selected: 0,
      currentView: 'item0',
      list: [
        {
          title: '11111'
        },
        {
          title: '22222'
        },
        {
          title: '33333'
        }
      ]
    };
  },
  methods: {
    change (index) {
      this.selected = index;
      this.currentView = 'item' + index; // 切换currentView
    }
  }
};
</script>

<style lang="css" scoped>
.clearfix:after {
  content: "\00A0";
  display: block;
  visibility: hidden;
  width: 0;
  height: 0;
  clear: both;
  font-size: 0;
  line-height: 0;
  overflow: hidden;
}
.clearfix {
  zoom: 1
}
ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}
.tab {
  width: 366px;
}
.hd li {
  float: left;
  border: 1px solid #666;
  padding: 12px 0;
  width: 120px;
  text-align: center;
  border-right: none;
}
.hd .active {
  background: #e85b4b;
  color: #fff;
  font-weight: bold;
}
.bd {
  padding-top: 10px;
}
.bd .item {
  display: none
}
.bd .active {
  display: block
}
</style>



