<template lang="html">
  <div>
    <time-down @time-end="message = '倒计时结束'" :endTime='endTime'></time-down>
    <p>{{message}}</p>
  </div>
</template>

<script>
import timeDown from '@/components/timeDown/timeDown.vue';
export default {
  data () {
    return {
      message: '正在倒计时',
      endTime: '2018-12-25 15:46:00'
    };
  },
  components: {
    timeDown
  }
};
</script>

<style lang="css">
</style>



