<template lang="html">
  <div class="TodoList">
    <h1>My Todo App!</h1>
    <div class="TodoListContent">
  		<BaseInputText
  			v-model="newTodoText"
  			placeholder="请输入待办事项"
  			@keydown.enter="addTodo"
  		/>
  		<ul v-if="todos.length">
  			<TodoListItem
  				v-for="todo in todos"
  				:key="todo.id"
  				:todo="todo"
  				@remove="removeTodo"
  			/>
  		</ul>
  		<p v-else>
  			暂时没有待办事项
  		</p>
  	</div>
  </div>
</template>

<script>
import BaseInputText from '@/components/BaseInputText/BaseInputText';
import TodoListItem from '@/components/TodoListItem/TodoListItem';
let nextTodoId = 1;
export default {
  data () {
    return {
      newTodoText: '',
      todos: [
        {
          id: nextTodoId++,
          text: 'Learn Vue'
        },
        {
          id: nextTodoId++,
          text: 'Learn about single-file components'
        },
        {
          id: nextTodoId++,
          text: 'Fall in love'
        }
      ]
    };
  },
  methods: {
    addTodo () {
      const trimmedText = this.newTodoText.trim();
      if (trimmedText) {
        this.todos.push({
          id: nextTodoId++,
          text: trimmedText
        });
        this.newTodoText = '';
      };
    },
    removeTodo (idToRemove) {
      this.todos = this.todos.filter(todo => {
        return todo.id !== idToRemove;
      });
    }
  },
  components: {
    BaseInputText, TodoListItem
  }
};
</script>

<style lang="css">
  *, *::before, *::after {
  	box-sizing: border-box;
  }
  .TodoList {
  	max-width: 400px;
  	margin: 0 auto;
  	line-height: 1.4;
  	font-family: 'Avenir', Helvetica, Arial, sans-serif;
  	-webkit-font-smoothing: antialiased;
  	-moz-osx-font-smoothing: grayscale;
  	color: #32485F;
  }
  h1 {
  	text-align: center;
  }
</style>



