<template lang="html">
  <div v-show=show>
    <img src="https://img.alicdn.com/simba/img/TB14sYVQXXXXXc1XXXXSutbFXXX.jpg" alt="">
    <img src="//img.alicdn.com/tfs/TB1iZ6EQXXXXXcsXFXXXXXXXXXX-520-280.jpg_q90_.webp" alt="">
    <img src="https://img.alicdn.com/simba/img/TB1C0dOPXXXXXarapXXSutbFXXX.jpg" alt="">
    <img src="//img.alicdn.com/tfs/TB1iZ6EQXXXXXcsXFXXXXXXXXXX-520-280.jpg_q90_.webp" alt="">
  </div>
</template>

<script>
export default {
  data () {
    return {
      count: 0,
      show: false
    };
  },
  mounted () {
    let imgs = document.querySelectorAll('img');
    console.log(imgs);
    Array.from(imgs).forEach((item) => {
      let img = new Image();
      // 只有赋值img.src后，才会走img.onload函数，才会count++
      img.onload = () => {
        this.count++;
      };
      console.log(item);
      img.src = item.getAttribute('src');
    });
  },
  watch: {
    count (val, oldval) {
      if (val === 4) {
        this.show = true;
        console.log('加载完毕');
        // 然后可以对后台发送一些ajax操作
      };
    }
  }
};
</script>

<style lang="css">
</style>



