<template lang="html">
  <button @click='a'>按钮</button>
</template>

<script>
export default {
  data () {
    return {
      time2: ''
    };
  },
  props: ['time'],
  watch: {
    time (value) {
      this.time2 = value;
    }
  },
  methods: {
    a () {
      this.time2++;
      this.$emit('a', this.time2);
    }
  }
};
</script>

<style lang="css">
</style>



