<template lang="html">
  <div class="">
    {{title}}
  </div>
</template>

<script>
export default {
  name: 'advertisement',
  computed: {
    title () {
      return this.$store.state.title;
    },
    changePeople () {
      return this.$store.getters.changePeople;
    }
  },
  created () {
    console.log(this.changePeople);
    const keyCode = sessionStorage.keyCode = 'keyCode';
    const keyWord = sessionStorage.keyWord = keyCode.split('_')[0];
    const hunterCode = sessionStorage.hunterCode = keyCode.split('_')[1];
    const sid = sessionStorage.sid = 'sid';
    const ck = sessionStorage.ck = 'ck';
    const tm = sessionStorage.tm = 'tm';
    this.$store.commit('getParam', {
      keyCode,
      keyWord,
      hunterCode,
      sid,
      ck,
      tm
    });
    this.$store.dispatch('getParamAsync', {
      keyCode,
      keyWord,
      hunterCode,
      sid,
      ck,
      tm
    });
  }
};
</script>

<style lang="css">
</style>



