<template lang="html">
  <div class="">
    <table class="table1">
        <thead>
          <tr>
            <th>#</th>
            <th>Name</th>
            <th>Age</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td><input type="checkbox" v-model="allChecked"/>全选{{allChecked}}</td>
          </tr>
          <tr v-for="item in list">
            <td><input type="checkbox" v-model="item.checked" /></td>
            <td>{{item.name}}</td>
            <td>{{item.age}}</td>
          </tr>
        </tbody>
    </table>
  </div>
</template>

<script>

export default {
  data () {
    return {
      list: [
        {
          name: '张三',
          age: 23,
          checked: false
        },
        {
          name: '李四',
          age: 20,
          checked: true
        },
        {
          name: '王五',
          age: 30,
          checked: true
        }
      ]
    };
  },
  computed: {
    // 全选状态
    allChecked: {
      get: function () {
        return this.checkedCount === this.list.length;
      },
      set: function (value) {
        this.list.forEach(function (item) {
          item.checked = value;
        });
        return value;
      }
    },
    // 计算出已选择的个数
    checkedCount: {
      get: function () {
        var i = 0;
        this.list.forEach(function (item) {
          if (item.checked === true) i++;
        });
        return i;
      }
    }
  }
};
</script>

<style lang="css">
</style>



