<template lang="html">
  <div>
    <p>输入框</p>
    <input type="text" name="" value="" v-model="message">
    <p>{{message}}</p>

    <p>单选框</p>
    <input type="radio" value="One" v-model="picked">One
    <input type="radio" value="Two" v-model="picked">Two
    <input type="radio" value="Three" v-model="picked">Three
    <p>Picked: {{ picked }}</p>

    <p>多选框</p>
    <input type="checkbox" value="foo" v-model="checkedVals">foo
    <input type="checkbox" value="bar" v-model="checkedVals">bar
    <input type="checkbox" value="baz" v-model="checkedVals">baz
    <p>checkedVals：{{checkedVals}}</p>

    <p>自定义组件单选框</p>
    <inputRadio name="radio" value="a" label="自定义单选框1" :modelValue="custom"></inputRadio>
    <inputRadio name="radio" value="b" label="自定义单选框2" :modelValue="custom"></inputRadio>
    <inputRadio name="radio" value="c" label="自定义单选框3" :modelValue="custom"></inputRadio>
    <inputRadio name="radio" value="d" label="自定义单选框4" :modelValue="custom"></inputRadio>
    <p>custom：{{custom}}</p>
  </div>
</template>

<script>
import inputRadio from '@/components/input-model/radio';
export default {
  data () {
    return {
      message: '输入框',
      picked: 'Three',
      checkedVals: ['bar'],
      custom: ''
    };
  },
  components: {
    inputRadio
  }
};
</script>

<style lang="css">
</style>



