<template lang="html">
  <input
    type="text"
    class="input"
    :value="value"
    v-on="listeners"
    v-focus
  >
</template>

<script>
export default {
  props: {
    value: {
      type: String,
      default: ''
    }
  },
  computed: {
    listeners () {
      return {
        // Pass all component listeners directly to input
        ...this.$listeners,
        // Override input listener to work with v-model
        input: event => this.$emit('input', event.target.value)
      };
    }
  },
  directives: {
    focus: {
      // 指令的定义
      // 当被绑定的元素插入到 DOM 中时……
      inserted: function (el) {
        // 聚焦元素
        el.focus();
      }
    }
  }
};
</script>

<style lang="css">
  .input {
    width: 100%;
    padding: 8px 10px;
    border: 1px solid #32485F;
  }
</style>



