<template lang="html">
  <div class="">
    <a href="javascript:;" @click="showtips">显示tips</a>
    <tips :tips-options="tipsOptions" ref="dialog" @yes="yes" v-cloak >
      <div slot="body">
        <p>hello world</p>
        <p>shay</p>
        <p>2s后自动关闭</p>
      </div>
    </tips>
  </div>
</template>

<script>
import tips from '@/components/tips/tips';
export default {
  data () {
    return {
      tipsOptions: {
        title: 'tip'
      }
    };
  },
  methods: {
    yes (args) {
      // console.log( args );
      alert(JSON.stringify(args));
    },
    showtips () {
      // console.log( this.$refs );
      this.$refs.dialog.showTips();
    }
  },
  components: {
    tips
  }
};
</script>

<style lang="css">
</style>



