<template lang="html">
  <component :is="spinner"></component>
</template>

<script>
// import spinnerSnake from '@/components/shaySpinner/snake.vue';
// import spinnerDoubleBounce from '@/components/shaySpinner/double-bounce.vue';
const SPINNERS = [ // 创建spinner组件种类
  'snake',
  'double-bounce'
];
const parseSpinner = function (index) {
  // 判断index类型
  if (Object.prototype.toString.call(index) === '[object Number]') {
    console.log(index);
    if (index >= SPINNERS.length) {
      console.warn(`'${index}' spinner not found, use the default spinner.`);
      index = 0; // 如果值大于数组长度则设置0为默认值
    };
    // 输出正确的数值
    return SPINNERS[index];
  }
  if (SPINNERS.indexOf(index) === -1) {
    console.warn(`'${index}' spinner not found, use the default spinner.`);
    index = SPINNERS[0];
  }
  return index;
};
export default {
  name: 'shay-spinner',
  computed: {
    spinner () { // 获取组件名称然后放到 :is 中
      console.log(`spinner-${parseSpinner(this.type)}`);
      return `spinner-${parseSpinner(this.type)}`;
    }
  },
  components: { // 引入组件实例
    spinnerSnake: resolve => require(['./snake.vue'], resolve),
    spinnerDoubleBounce: resolve => require(['./double-bounce.vue'], resolve)
  },
  props: {
    type: {
      default: 0
    },
    size: {
      type: Number,
      default: 50
    },
    color: {
      type: String,
      default: 'red'
    }
  }
};
</script>

<style lang="css">
</style>



