<template lang="html">
  <div class="tips" v-show="show" transition="fade">
    <div class="tips-close" @click="closeTips">x</div>
    <div class="tips-header">
      <slot name="header">
        <p class="title">{{tips.title}}</p>
      </slot>
    </div>
    <div class="tips-body">
      <slot name="body">
        <p class="notice">{{tips.text}}</p>
      </slot>
    </div>
    <div class="tips-footer">
      <a href="javascript:;" v-if="tips.showbtn" @click="yes" >{{tips.btnText}}</a>
    </div>
  </div>
</template>

<script>
export default {
  props: ['tipsOptions'],
  data () {
    return {
      show: false
    };
  },
  computed: {
    tips: {
      get () {
        let tips = this.tipsOptions || {};
        tips = { // 设置默认值
          title: tips.title || '提示',
          text: tips.text || '',
          showbtn: tips.showbtn || true,
          btnText: tips.btnText || '确定'
        };
        // console.log(tips);
        return tips;
      }
    }
  },
  methods: {
    closeTips () {
      this.show = false;
    },
    yes () {
      this.show = false;
      this.$emit('yes', {name: 'shay', age: 24});
    },
    showTips () {
      var self = this;
      self.show = true;
      setTimeout(function () {
        self.show = false;
      }, 2000);
    }
  }
};
</script>

<style lang="css">
.tips {
  position: fixed;
  left: 10px;
  bottom: 10px;
  z-index: 1001;
  -webkit-overflow-scrolling: touch;
  max-width: 690px;
  width: 260px;
  padding: 10px;
  background: #fff;
  box-shadow: 0 0 10px #888;
  border-radius: 4px;
}
.tips-close{
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 20px;
  line-height: 20px;
  text-align: center;
  cursor: pointer;
}
.tips-header{
  text-align: center;
  font-size: 25px;
}
</style>



