<template lang="html">
  <div class="">
    import方法：token（global_.token）：{{token}}<br>
    vue挂载方法：userSite（this.GLOBAL.userSite）：{{userSite}}<br>
    <button @click="clickGlobal">点击全局函数(import方法)</button>
    <button @click="clickGlobalThis">点击全局函数(vue挂载方法)</button><br>
    <button @click="changeId">切换全局变量id(点击后切换路由地址)</button><br>
    id = {{id}}<br>
  </div>
</template>

<script>
import global_ from '@/components/globalVar/globalVar'; // 引用模块进来
export default {
  data () {
    return {
      token: global_.token, // 将全局变量赋值到data里面
      userSite: this.GLOBAL.userSite,
      id: global_.id,
      firstName: 'Foo',
      lastName: 'Bar'
    };
  },
  methods: {
    clickGlobal () {
      global_.globalAlert();
    },
    clickGlobalThis () {
      this.GLOBAL.globalAlert();
    },
    changeId () {
      let n = Math.floor(Math.random() * 10 + 1);
      global_.id = n;
      console.log(global_.id);
      console.log(this.id);
      return n;
    }
  }
};
</script>

<style lang="css">
</style>



